
import { Options, Vue } from "vue-class-component";
import BottomNavigationBar from "@/components/BottomNavigationBar.vue";
import TopBar from "@/components/TopBar.vue";
import PopupMenu from "@/components/PopupMenu.vue";
import PopupMenuItem from "@/components/PopupMenuItem.vue";
import { EventEmitter2, Listener } from "eventemitter2";
import MessageColumn from "@/components/MessageColumn.vue";
import { State } from "vuex-class";
import Hub from "@/hub";
import dayjs from "dayjs";
import Icons from "@/components/Icons.vue";
// import { forEach } from "lodash";

@Options({
  components: {
    BottomNavigationBar,
    TopBar,
    PopupMenu,
    PopupMenuItem,
    MessageColumn,
    Icons,
  },
})
export default class MessageList extends Vue {
  @State("hub") hub!: Hub;
  @State("emitter") emitter!: EventEmitter2;
  ListenerList: Listener[] = [];
  users = [];
  msg!: string;
  async created() {
    // this.users.length = 0;
    this.loadData();
    var listener = this.emitter.on("friends.*", this.addChat.bind(this), {
      objectify: true,
    }) as Listener;
    console.log(listener);
    this.ListenerList.push(listener);
  }

  unmounted() {
    this.ListenerList.forEach((listener) => {
      listener.off();
    });
    this.ListenerList = [];
  }

  async addChat(data: any) {
    // console.log(data);
    this.loadData();
  }

  async loadData() {
    var response = await this.hub.invoke<[]>("User", "GetMessageList");
    this.users = [];
    response.forEach((item) => {
      this.users.push(item);
      // console.log(item);
    });
  }

  timenow(txt: string) {
    return dayjs(txt).format("YYYY/MM/DD HH:mm");
  }
}
