
import { Options, Vue } from "vue-class-component";

@Options({
  props: {
    src: String,
    show: {
      type: Boolean,
      default: false,
    },
  },
})
export default class PopupMenu extends Vue {
  src!: string;
  show!: boolean;
  _show = this.show;
  switchShow() {
    this._show = !this._show;
  }
}
