
import { Options, Vue } from "vue-class-component";
import Icons from "@/components/Icons.vue";

@Options({
  components: {
    Icons,
  },
  props: {
    to: String,
    src: String,
    name: String,
    content: String,
    time: String,
    path: String,
  },
  watch: {},
})
export default class MessageColumn extends Vue {
  _src!: any;
  _name!: string;
  _to!: string;
  _content!: string;
  _time!: string;
  _path!: string;
  created() {
    this.loadData();
  }

  loadData() {
    const _this = this as any;
    this._src = _this.src;
    this._name = _this.name;
    // this._content = _this.content;
    this._time = _this.time;
    this._path = _this.path;
    this._to = _this.to;
    if (_this.path == "/system_session") {
      this._to = "/systeminforms";
      this._name = "系统消息";
      this._src = "logo";
      //系统消息自定义显示
    } else if (_this.path == "/global_chat") {
      this._to = "/globalchat";
      // this._name = "系统消息";
      // this._src = "logo";
      //世界消息自定义信息
    }
  }
}
